.galleryList {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
  /* display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 25px;
  /* padding: 40px; */
  /* max-width: 1000px; */
  /* width: 100%;
  list-style: none;
  text-align: center;
  align-items: center; */
}

.galleryItem {
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}
.imageTitle {
  height: 64px;
  text-align: center;
}
/* .imgContainer {
  width: 400px;
} */
.galleryImg {
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  /* width: 300px; */
}

.galleryImg:hover {
  transform: scale(1.03);
  cursor: zoom-in;
}
